import { LitElement, css, html } from 'lit';
import { property, customElement } from 'lit/decorators.js';
import { resolveRouterPath } from '../router';

import '@shoelace-style/shoelace/dist/components/card/card.js';
import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/icon-button/icon-button.js';

import { styles } from '../styles/shared-styles';

@customElement('app-home')
export class AppHome extends LitElement {

  // For more information on using properties and state in lit
  // check out this link https://lit.dev/docs/components/properties/
  @property() message = `NELOVEK Q\u0308OBO MOKOIT`;

  static styles = [
    styles,
    css`
    #welcomeBar {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    #welcomeCard,
    #infoCard {
      padding: 18px;
      padding-top: 0px;
    }

    sl-card::part(footer) {
      display: flex;
      justify-content: flex-end;
    }

    @media(min-width: 750px) {
      sl-card {
        width: 70vw;
      }
    }


    @media (horizontal-viewport-segments: 2) {
      #welcomeBar {
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
      }

      #welcomeCard {
        margin-right: 64px;
      }
    }

    .main-buttons-wrapper { display: flex; flex-direction: row; justify-content: center; position: relative;}
    /*.main-buttons-wrapper sl-button { margin: var(--sl-spacing-3x-large); }*/
    .main-buttons-wrapper a { text-decoration:  none; }

    .button-label {
      color: var(--color-scheme-03);
      display: flex;
      flex-direction: column;
      gap: 4px;
      justify-content: center;
      align-items: center;
      margin: var(--sl-spacing-large);
    }
    .button-label sl-icon { font-size: 48px; }



  `];

  async firstUpdated() {
    // this method is a lifecycle even in lit
    // for more info check out the lit docs https://lit.dev/docs/components/lifecycle/
    console.log('This is your home page');
  }

  share() {
    if ((navigator as any).share) {
      (navigator as any).share({
        title: 'Museo ' + this.message,
        text: 'Visita el museo!',
        url: 'https://museonelovekqobomokoit.com.ar',
      });
    }
  }

  render() {
    return html`

    <div class="page-wrapper">
      <div class="background-image"></div>
      <div class="container">
          <div class="row">
            <div class="col">

              <top-app-bar>
                <div slot="leading">
                  <sl-icon-button name="list" library="nqm-icons"></sl-icon-button>
                </div>
                <div slot="heading">Museo NQ\u0308M</div>
              </top-app-bar>


              <main>
              <div id="welcomeBar">
                <sl-card id="welcomeCard">
                  <div slot="header">
                    <h2>${this.message}</h2>
                  </div>

                  <p>
                    El Museo de Historia y Arqueologa Originaria forma parte de un programa de Rescate y Revalorizacin de la identidad del Pueblo Mocov de Colonia Dolores que est llevando adelante la Comisin Aborigen Aim Mokoilek con el asesoramiento de un equipo de investigacin de la Universidad Nacional de Rosario.
                  </p>

                  ${'share' in navigator
                    ? html`<sl-button slot="footer" variant="default" @click="${this.share}">
                              <sl-icon slot="prefix" name="share"></sl-icon>
                              Share this Starter!
                            </sl-button>`
                    : null}
                </sl-card>



                <!--<sl-button href="${resolveRouterPath('about')}" variant="primary">Navigate to About</sl-button>-->
              </div>
            </main>

            <footer>
              <div class="main-buttons-wrapper">

                <a href="">
                  <div class="button-label">
                    <sl-icon name="person-walking" library="nqm-icons"></sl-icon>
                    <div class="label">Visita libre</div>
                  </div>
                </a>
                <a href="">
                  <div class="button-label">
                    <sl-icon name="headphones" library="nqm-icons"></sl-icon>
                    <div class="label">Visita guiada</div>
                  </div>
                </a>

              </div>

            </footer>
          </div>
        </div>
      </div>

    </div>

    `;
  }
}
